<template>
    <div class="row mb-3">
        <div class="col">
            <div class="row">
                <div class="col align-self-start">
                    <h5>{{id}}</h5>
                </div>
                <div class="col-1 align-self-end">
                    <h5><i v-bind:class=get_expand_icon @click="toggle_expand()" style="cursor: pointer;"></i></h5>
                </div>
            </div>
            <hr />
            <div v-bind:class=get_expand_class>
                <p v-for="line in reversed" :key="line">{{line}}</p>
            </div>
            <hr />
        </div>
    </div>
</template>

<script setup>
import { computed, ref } from "vue";
const props = defineProps({
    id: String,
    data: Object
});
const expanded = ref(false)
function toggle_expand() {
    expanded.value = !expanded.value
}
const get_expand_class = computed(() => {
    if (expanded.value) {
        return ""
    } else {
        return "log"
    }
})
const get_expand_icon = computed(() => {
    if (expanded.value) {
        return "bi bi-chevron-up"
    } else {
        return "bi bi-chevron-down"
    }
})
const reversed = computed(() => {
    var logs = props.data;
    return logs.reverse();
});

</script>

<style scoped>
.log {
    display: flex;
    flex-direction: column;
    max-height: 250px;
    overflow-y: scroll;
    mask-image: linear-gradient(to bottom, black 75%, transparent 100%);
    padding-bottom: 40px;
}
</style>