<template>
    <div class="row mb-3">
        <div class="col">
            <h5> {{ id }}</h5>
            <Line :chart-options="chartOptions" :chart-data="chartData.value" />
        </div>
    </div>
</template>

<script setup>
import { computed } from "vue";
import { Line } from 'vue-chartjs';
import {
    Chart as ChartJS,
    Title,
    Tooltip,
    Legend,
    LineElement,
    LinearScale,
    PointElement,
    CategoryScale,
} from 'chart.js'

ChartJS.register(
    Title,
    Tooltip,
    Legend,
    LineElement,
    LinearScale,
    PointElement,
    CategoryScale
)

const props = defineProps({
    id: String,
    data: Object
})

const chartData = computed(() => {
    return { value: {
    labels: props.data.x,
    datasets: [
        {
            label: "test",
            backgroundColor: '#f87979',
            data: props.data.y
        }
    ]
}}})

const chartOptions = {
    responsive: true,
    maintainAspectRatio: false
}

</script>

